<template>
  <div>
    <!-- 上方下拉菜单 -->
    <van-row>
      <van-col span="8">
        <van-dropdown-menu>
          <van-dropdown-item title="分类" ref="item">
            <div class="lzh-contbox">
              <div class="lzh-contai">
                <p>年级</p>
                <div class="lzh-container">
                  <span>初一</span>
                  <span>初二</span>
                  <span>初三</span>
                  <span>高一</span>
                  <span>高二</span>
                </div>
              </div>
            </div>
            <div class="lzh-contbox">
              <div class="lzh-contai">
                <p>学科</p>
                <div class="lzh-container">
                  <span>语文</span>
                  <span>数学</span>
                  <span>英语</span>
                  <span>物理</span>
                  <span>化学</span>
                </div>
              </div>
            </div>
            <div class="lzh-butbox">
              <van-button
                type="danger"
                style="background: #fff; color: #000; border: 1px solid #d9d9d9"
                >重置</van-button
              >
              <van-button
                type="danger"
                style="background: #eb6100; color: #fff"
              >
                确认
              </van-button>
            </div>
          </van-dropdown-item>
        </van-dropdown-menu>
      </van-col>
      <van-col span="8">
        <van-dropdown-menu>
          <van-dropdown-item title="排序" v-model="value1" :options="option1" />
        </van-dropdown-menu>
      </van-col>
      <van-col span="8">
        <van-dropdown-menu>
          <van-dropdown-item title="筛选" ref="item">
            <div class="lzh-contbox">
              <div class="lzh-contai">
                <div class="lzh-container">
                  <span  ref="item"
                    v-for="(item, index) in appCourseType"
                    :key="index"
                    @click="onScreen(item.id)"
                    >{{ item.name }}</span
                  >
                </div>
              </div>
            </div>
          </van-dropdown-item>
        </van-dropdown-menu>
      </van-col>
    </van-row>
    <!-- 课程列表 -->
    <div class="lzh-title-box">
      <div
        class="lzh-container"
        v-for="(item, index) in list.list"
        :key="index"
      >
        <div class="lzh-teseke" @click="courseitem(item.id)">
          <div style="margin-left: 0.4rem">{{ item.title }}</div>
          <div style="margin-left: 0.4rem">共8课时</div>
          <div class="lzh-titlebox">
            <div class="lzh-titleleft">
              <img
                round
                width="1.5rem"
                height="1.5rem"
                :src="item.teachers_list[0].teacher_avatar"
              />{{ item.teachers_list[0].teacher_name }}
            </div>
            <div class="lzh-titleright">
              <img v-show="item.has_buy" src="/baoming.png" alt="" />
            </div>
          </div>

          <div class="lzh-titlebox" style="border-bottom: 0">
            <div class="lzh-titleleft">
              <div>132人报名</div>
            </div>
            <div class="lzh-titleright">
              <div style="color: aquamarine">免费</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="height: 3rem"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value1: 0,
      option1: [
        { text: "综合排序", value: 0 },
        { text: "最新", value: 1 },
        { text: "最热", value: 2 },
        { text: "价格从低到高", value: 3 },
        { text: "价格从高到低", value: 4 },
      ],
      list: [], //列表数据
      copylist: [],
      appCourseType: [],
    };
  },
  mounted() {
    //特色课分类
    this.$APP.courseClassify().then((res) => {
      console.log(res.data.data.appCourseType);
      this.appCourseType = res.data.data.appCourseType;
    });
    //特色课列表
    this.$APP.courseBasis().then((res) => {
      console.log(res.data);
      this.list = res.data.data;
      this.copylist = res.data.data;
    });
  },
  methods: {
    //进入课程详情页
    courseitem(id) {
      console.log(id);
      this.$router.push({
        path: "/courseitem",
        query: {
          id,
        },
      });
    },
    //点击筛选的选项
    onScreen(id) {
      console.log(id);
      this.$APP
        .courseBasis({
          params: {
            page: 1,
            limit: 10,
            course_type: id,
            classify_id: "",
            order_by: "",
            attr_val_id: "",
            is_vip: 0,
          },
        })
        .then((res) => {
          console.log(res.data.data);
          this.list = res.data.data;
          this.$refs.item.toggle();
        });
    },
  },
};
</script>

<style>
.van-cell__value {
  display: none;
}
.van-dropdown-item__option {
  text-align: center;
}
.van-dropdown-menu__bar {
  box-shadow: 0 0 0;
}
.lzh-contbox {
  padding-left: 1rem;
  font-size: 0.3rem;
}
.lzh-contai {
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 0.5rem;
}
.lzh-container {
  width: 100%;
  height: 6rem;
  background: #fff;
  border-radius: 5px;
  margin-top: 0.5rem;
  display: inline-flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
}
.lzh-container span {
  width: 3rem;
  height: 1.5rem;
  line-height: 1.5rem;
  background: #f5f5f5;
  text-align: center;
  margin-right: 0.7rem;
  margin-bottom: 0.8rem;
}
.lzh-butbox {
  width: 100%;
}
.lzh-butbox {
  width: 100%;
  padding: 0.5rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
}
.lzh-butbox button {
  width: 6rem;
  height: 1.3rem;
}
.lzh-title-box {
  padding: 0.5rem;
  background: #f0f2f5;
}
.lzh-teseke {
  width: 100%;
  height: 100%;
  font-size: 0.7rem;
}
.lzh-titlebox {
  width: 90%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgb(177, 175, 175);
  margin-left: 0.7rem;
}
.lzh-titleleft {
  width: 40%;
  height: 1.5rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
}
.lzh-titleleft img {
  width: 1.5rem;
  height: 1.5rem;
}
.lzh-tilteright {
  width: 60%;
}
.lzh-titleright img {
  width: 3rem;
}
</style>